'use client';

import { createClient } from '@/lib/supabase/client';
import { useEffect, useState } from 'react';
import { History, CheckCircle, XCircle, Brain } from 'lucide-react';
import { Log, Alias } from '@/types/database';

export default function LogsPage() {
  const supabase = createClient();
  const [logs, setLogs] = useState<Log[]>([]);
  const [aliases, setAliases] = useState<Alias[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    setIsLoading(true);
    const { data: aliasData } = await supabase.from('aliases').select('*');
    const { data: logData } = await supabase
      .from('logs')
      .select('*')
      .order('created_at', { ascending: false });

    if (aliasData) setAliases(aliasData);
    if (logData) setLogs(logData);
    
    setIsLoading(false);
  };

  return (
    <div className="max-w-6xl mx-auto space-y-8">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Activity History</h1>
        <button 
          onClick={fetchData}
          className="text-sm text-blue-600 hover:text-blue-800 font-medium"
        >
          Refresh Feed
        </button>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-100 overflow-hidden">
        {isLoading ? (
          <div className="p-8 text-center text-gray-500">Loading activity logs...</div>
        ) : logs.length === 0 ? (
          <div className="p-12 text-center space-y-4">
            <div className="flex justify-center">
              <History size={48} className="text-gray-300" />
            </div>
            <p className="text-gray-500 max-w-sm mx-auto">
              No activity recorded yet. Once you start receiving emails to your aliases, they will appear here.
            </p>
          </div>
        ) : (
          <table className="w-full text-left border-collapse">
            <thead>
              <tr className="bg-gray-50 border-b border-gray-100">
                <th className="px-6 py-4 font-semibold text-gray-700">Timestamp</th>
                <th className="px-6 py-4 font-semibold text-gray-700">Alias</th>
                <th className="px-6 py-4 font-semibold text-gray-700">Sender / Subject</th>
                <th className="px-6 py-4 font-semibold text-gray-700">AI Summary</th>
                <th className="px-6 py-4 font-semibold text-gray-700 text-center">Status</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-100">
              {logs.map((log) => {
                const alias = aliases.find(a => a.id === log.alias_id);
                return (
                  <tr key={log.id} className="hover:bg-gray-50 transition-colors">
                    <td className="px-6 py-4 text-xs text-gray-400">
                      {new Date(log.created_at).toLocaleString()}
                    </td>
                    <td className="px-6 py-4">
                      <span className="text-sm font-medium text-gray-700">{alias?.address || 'Deleted Alias'}</span>
                    </td>
                    <td className="px-6 py-4">
                      <div className="font-medium text-gray-900 truncate max-w-xs">{log.subject}</div>
                      <div className="text-sm text-gray-500 truncate max-w-xs">{log.sender}</div>
                    </td>
                    <td className="px-6 py-4">
                      {log.ai_summary ? (
                        <div className="flex items-start gap-2 bg-blue-50 p-2 rounded-lg border border-blue-100 max-w-sm">
                          <Brain size={16} className="text-blue-600 mt-1 shrink-0" />
                          <span className="text-sm text-blue-900 italic">"{log.ai_summary}"</span>
                        </div>
                      ) : (
                        <span className="text-xs text-gray-400 italic">No summary</span>
                      )}
                    </td>
                    <td className="px-6 py-4 text-center">
                      <div className="flex justify-center">
                        {log.status === 'forwarded' ? (
                          <div className="flex items-center gap-1 text-green-600 bg-green-50 px-2 py-1 rounded-full text-xs font-bold uppercase tracking-tighter">
                            <CheckCircle size={14} />
                            Forwarded
                          </div>
                        ) : (
                          <div className="flex items-center gap-1 text-red-600 bg-red-50 px-2 py-1 rounded-full text-xs font-bold uppercase tracking-tighter">
                            <XCircle size={14} />
                            Blocked
                          </div>
                        )}
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}
